// import uniSegmentedControl from '@/components/tab-control.vue';
import uniSegmentedControl from '@/components/uni-segmented-control.vue';
import {
	getUserinit
} from '@/api/user.js';
import {
	getnowbalance,
	tx_zfb
} from '@/api/comission.js';
import {
	getGoodsList,
	getRecommend,
	getHotListGood,
	getCTK,
	getOtherGood,
	exisitCollection,
	existUser,
	getDescImg
} from '@/api/goods.js'
export default {
	data() {
		return {
			title: '我的',
			headIcon: 'https://img-cdn-qiniu.dcloud.net.cn/uniapp/images/shuijiao.jpg?imageView2/3/w/200/h/100/q/90',
			msg: [
				'买药不出门省时省力省口罩',
				'健康好物速享 春季防护指南',
				'2020年天猫品智315—主会场'
			],
			items: [
				'淘宝',
			],
			current: 0,
			activeColor: '#E11C1E',
			styleType: 'text',
			user: new Object(),
			tbsqFlag: uni.getStorageSync("tbsqFlag"),
			user_data: null, //用户佣金信息返回
		}
	},
	components: {
		uniSegmentedControl
	},
	onShow(e) {
		this.user = uni.getStorageSync('user');
		this.existUser();
		this.getUser();
	},
	onPullDownRefresh(e) {
		console.log(e);
		uni.stopPullDownRefresh();
	},
	onLoad() {
		console.log(uni.getStorageSync('user'));
		tx_zfb({
			alipay:'13008847296',
			realname:'曾磊',
			money:1,
			beizhu:'app测试',
			thirdorder:''
		}).then(res=>{
			console.log(res);
		})
		console.log(uni.getStorageSync('user'));
		uni.setNavigationBarTitle({
			title: this.title
		})
		//获取佣金信息
		this.getCommission();
		uni.removeStorageSync("tbsqFlag");
		this.getData(); //再次获取授权信息  避免授权之后  界面未更新
	},
	methods: {
		copykey(){
			
		},
		team_list(){
			uni.navigateTo({
				url:'/pages/user/team_list'
			})
		},
		moreData(){
			uni.navigateTo({
				url:'/pages/user/revenue'
			})
		},
		orderDetail(index){
			switch(index){
				case 1:
					uni.navigateTo({
						url:'/pages/user/revenue'
					})
				break;
				case 2:
					uni.navigateTo({
						url:'/pages/user/board'
					})
				break;
				case 3:
					uni.navigateTo({
						url:'/pages/user/my_team'
					})
				break;
				case 4:
					uni.navigateTo({
						url:'/pages/user/invite_friend'
					})
				break;
			}
		},
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index
			}
		},
		edit_info() {
			uni.navigateTo({
				url: '/pages/user/my_info?user=' + this.user + '&tbsqFlag=' + this.tbsqFlag
			})
		},

		existUser() {
			if (!uni.getStorageSync("user")) {
				uni.reLaunch({
					url: '/pages/index/login'
				})
				return;
			}
			console.log(this.user);
			existUser(this.user.phone).then(res => {
				if (res.data.code != 200) {
					uni.showToast({
						title: "用户不存在!",
						icon: "none"
					})
					uni.reLaunch({
						url: '/pages/index/login'
					})
				}
			})
		},
		tbsq() {
			if (this.tbsqFlag) {
				this.tbsqFlag = this.tbsqFlag ? false : true;
				return;
			}
			const bcPlugin = uni.requireNativePlugin('dahui-alibaichuan');
			bcPlugin.BCAuth(result => {
				this.tbsqFlag = true;
				uni.setStorageSync("tbsqFlag", true);
				//code == 0 ,msg:授权成功  ； code==-1  ,msg:报错信息
				var url = 'https://oauth.taobao.com/authorize?response_type=code&client_id=25901417&redirect_uri=' +
					'http://39.108.215.49:8009/html/code.html&state=' +
					uni.getStorageSync("user").phone + '&view=wap'
				bcPlugin.BCGetCoupons(url, result => {
					console.log(22);
				});

			});
		},
		customer() {
			uni.showModal({
				title: '专属客服',
				content: '添加微信：xxxxx获取一对一专属服务',
				showCancel: false,
				confirmText: '知道啦',
			});
		},
		getCommission() {
			getnowbalance({
				pid:this.user.pid
			}).then(res => {
				console.log(res);
			})
		},
		getUser() {
			console.log(uni.getStorageSync('user'));
			let ret = getUserinit(uni.getStorageSync('user').phone)
			ret.then(res => {
				console.log('佣金获取', res)
				if (res.code == 100) {
					uni.showToast({
						title: "初始化用户信息失败",
						icon: 'none'
					})
					return
				} else {
					// this.user.username = data.username;
					// this.user.pid = data.PID;
					// this.user.ye = data.CommissionSurplus;
					// this.user.zfbname = data.zfbname;
					// this.user.avatar = data.avatar;
					uni.setStorageSync('user_data', res.result)
					this.user_data = uni.getStorageSync('user_data');
					if (uni.getStorageSync("user").pid == "") {
						this.tbsqFlag = false;
						uni.setStorageSync("tbsqFlag", false);
					} else {
						this.tbsqFlag = true;
						uni.setStorageSync("tbsqFlag", true);
					}
				}
			})
		},
		getData() {
			try {
				uni.setStorageSync("invitecode", uni.getStorageSync('user').invitecode)
				if (!uni.getStorageSync('user')) {
					uni.reLaunch({
						url: '/pages/index/login'
					})
					return
				}
				uni.showLoading({
					title: '加载中...'
				})
				this.getUser();
				uni.hideLoading()
			} catch (e) {
				uni.hideLoading()
				uni.showToast({
					title: "初始化用户信息失败",
					icon: 'none'
				})
				console.log('error', e.message);
			}
			//数据预加载
			this.randomfn();
			console.log(uni.getStorageSync("goodsList"))
		},
		randomfn() {
			uni.removeStorageSync("goodsList")
			let ary = [];
			for (let i = 0; i < this.tabBars.length; i++) {
				if (i < 1) {
					getGoodsList({
						page: this.page,
						type: this.tabBars[i].name
					}).then(res => {
						let aryItem = {
							data: []
						};
						console.log(this.tabBars[i].name);
						aryItem.data = res.result;
						ary.push(aryItem);
						this.goodsList.push(aryItem);
						uni.setStorageSync("goodsList", this.goodsList);
					});
				}
			}
			return ary;
		},
		jump(url) {
			if (url == 'tx') {
				uni.showModal({
					title: '爱省购提醒',
					content: '功能开发中...',
					showCancel: false,
					confirmText: '知道啦',
				});
			}
			uni.navigateTo({
				url: `/pages/index/${url}`
			})
		},
		copy(str) {
			var params = new Object();
			params.scene = 'WXSceneSession',
				params.summary = str;
			share.wxContent(params);
			uni.setClipboardData({
				data: str,
				success: () => {
					uni.showToast({
						title: '已复制邀请码',
						icon: 'success',
					})
				}
			})
		}
	}
}
